@charset "UTF-8";

/* Custom fonts */
@font-face {
	font-family: 'Rubik';
	font-style: normal;
	font-weight: 300;
	font-display: swap;
	src: local('Rubik Light'), local('Rubik-Light'), url("../fonts/rubik-light.woff2") format('woff2');
	unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
	font-family: 'Rubik';
	font-style: normal;
	font-weight: 400;
	font-display: swap;
	src: local('Rubik'), local('Rubik-Regular'), url("../fonts/rubik-regular.woff2") format('woff2');
	unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
	font-family: 'Rubik';
	font-style: normal;
	font-weight: 500;
	font-display: swap;
	src: local('Rubik Medium'), local('Rubik-Medium'), url("../fonts/rubik-medium.woff2") format('woff2');
	unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
	font-family: 'Rubik';
	font-style: normal;
	font-weight: 700;
	font-display: swap;
	src: local('Rubik Bold'), local('Rubik-Bold'), url("../fonts/rubik-bold.woff2") format('woff2');
	unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

/* Variables */
:root {
	--pic-w: 181px;
	--pic-h: 309px;
	--pic-m: 10px;

	--padx: 20px;
	--pady: 17.5px;

	--pads: 7.5px;
	--padm: 15px;
	--padl: 30px;
	--padxl: 40px;
	--padxxl: 60px;

	--animation: background-color .5s ease, color .25s ease;
}

@media screen and (min-width: 700px) {
	:root {
		--pic-w: 271.5px;
		--pic-h: 463.5px;
		--pic-m: 20px;

		--padx: 40px;
		--pady: 35px;

		--pads: 10px;
		--padm: 20px;
		--padl: 40px;
		--padxl: 65px;
		--padxxl: 100px;
	}
}

/* Color Schemes */
:root {
	--black: #222;
	--white: #fff;
	--dark-white: #E8EAEB;
	--yellow: #FFD700;
	--black-75: rgba(0, 0, 0, 0.5);
	--black-50: rgba(0, 0, 0, 0.25);
	--black-25: rgba(0, 0, 0, 0.15);
	--black-15: rgba(0, 0, 0, 0.05);
	--black-gray: #111111;
	--black-gray-light: #131313;
	--gray-light: #C4C4C4;
	--red: #FF0F1E;
	--lime: #60e670;
}

.dark:root {
	--black: #ccc;
	--white: #000;
	--dark-white: #111;
	--yellow: #705f02;
	--black-75: rgba(255, 255, 255, 0.50);
	--black-50: rgba(255, 255, 255, 0.25);
	--black-25: rgba(255, 255, 255, 0.15);
	--black-15: rgba(255, 255, 255, 0.05);
	--black-gray: #bbb;
	--black-gray-light: #aaa;
	--gray-light: #333;
	--red: #B4352C;
	--lime: #0d4f03;
}

/* Giving persitent size for all tags */
* {
	box-sizing: border-box;
	outline: none;
}

/* Costumize selection color */
::-moz-selection {
	background: var(--yellow);
	text-shadow: none;
}

::selection {
	background: var(--yellow);
	text-shadow: none;
}


/* Costumize placeholder */
::-webkit-input-placeholder {
	color: var(--gray-light);
}

:-moz-placeholder {
	color: var(--gray-light);
	opacity: 1;
}

::-moz-placeholder {
	color: var(--gray-light);
	opacity: 1;
}

:-ms-input-placeholder {
	color: var(--gray-light);
}

::-ms-input-placeholder {
	color: var(--gray-light);
}

::placeholder {
	color: var(--gray-light);
}


#log {
	position: fixed;
	padding: 0 16px;
	top: 10px;
	left: 50%;
	color: white;
	background-color: black;
	border-radius: 4px;
	transform: translate3d(-50%, 0, 0);
	z-index: 9999;
	font-size: .7rem;
}


/* Defining the font size based on aspect ratio */
html {
	line-height: 150%;
	letter-spacing: 0.02em;
	color: var(--black);
	background-color: var(--white);
}

html.snap {
	overflow-x: hidden;
	overflow-y: scroll;
	scroll-snap-type: y mandatory;
	scroll-snap-stop: always;
}

/* Common styles */

.middle {
	display: block;
	width: 100vw;
	height: 100vh;
}

main {
	display: block;
	position: relative;
}

.middle {
	scroll-snap-align: center;
}

.middle.snap-start {
	scroll-snap-align: start;
}

.middle.snap-end {
	scroll-snap-align: end;
}

.middle.snap-none {
	scroll-snap-align: none;
}

.middle.height-auto {
	height: auto;
}

.no-js .middle {
	border-bottom: 1px dashed var(--gray-light);
}

.middle.auto {
	min-height: auto;
}

.middle.empty {
	display: none;
}

body,
html {
	font-family: "Rubik", sans-serif;
	letter-spacing: .065rem;
}

h1,
h2,
h3,
h4,
p {
	margin: 0;
	padding: 0;
	will-change: opacity, transform;
}

h1,
h2,
h3,
h4,
p,
span,
li {
	transition: color .5s ease;
}

h1 *,
h2 *,
h3 *,
h4 * {
	transition: initial;
}

h1,
h2,
h3,
h4 {
	font-weight: 500;
}

h2,
h3,
h4,
p {
	color: var(--black-gray-light);
}

h1 {
	margin: 0 0 var(--padl) 0;
	font-size: 5rem;
	line-height: 80%;
	letter-spacing: -0.06em;
}

h2 {
	margin: 0 0 var(--padl);
	font-size: 3.2rem;
	line-height: 90%;
	letter-spacing: -0.02em;
}

h3 {
	margin: 0 0 var(--padm);
	font-size: 2rem;
	line-height: 100%;
}

h4 {
	margin: 0 0 var(--padm);
	font-size: 1.4rem;
	line-height: 110%;
}

p {
	font-size: 1rem;
	line-height: 175%;
	opacity: .8;
	letter-spacing: 0.02em;
}

p a {
	text-decoration: underline;
}

a,
.spring {
	position: relative;
	display: inline-block;
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

.spring:before {
	content: "";
	display: block;
	position: absolute;
	z-index: -1;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: var(--yellow);
	opacity: 0;
	border-radius: 8px;
	pointer-events: none;
	transform: scale(1.15);
	transition: opacity .2s ease, transform .3s cubic-bezier(.59, 1.83, .81, 1);
	will-change: opacity, transform;
}

.spring.big:before {
	top: -4px;
	bottom: -4px;
	left: -12px;
	right: -12px;
}

.spring.superbig:before {
	top: -8px;
	bottom: -8px;
	left: -20px;
	right: -20px;
}

.spring:hover:before {
	opacity: 1;
	transform: scale(1);
}

.dark .spring:hover {
	color: var(--white);
}

small {
	display: inline-block;
	font-size: .8rem;
	line-height: 12px;
	letter-spacing: 0.14em;
}

/* Basic HR styling */
hr {
	display: block;
	height: 1px;
	border: 0;
	border-top: 1px solid var(--gray-light);
	margin: 1em 0;
	padding: 0;
}

hr.horizontal {
	height: var(--padxxl);
	border-top: 0;
	border-left: 1px solid var(--gray-light);
}

/*
 Remove the gap between audio, canvas, iframes,
 images, videos and the bottom of their containers:
 https://github.com/h5bp/html5-boilerplate/issues/440
*/
audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

/* Remove default fieldset styles */
fieldset {
	border: 0;
	margin: 0;
	padding: 0;
}

/* Allow only vertical resizing of textareas. */
textarea {
	resize: vertical;
}

/* make img inside picture tag follow picture size */
picture img {
	width: 100% !important;
	height: auto !important;
}

/* Browser Upgrade Prompt */
.browserupgrade {
	margin: 0.2em 0;
	background: var(--yellow);
	color: var(--black);
	padding: 0.2em 0;
}

.support {
	display: none;
	position: fixed;
	z-index: 999999;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	padding: 40px;
	flex-flow: row;
	align-items: center;
	justify-content: center;
	background-color: inherit;
}

.support .content {
	position: relative;
	display: flex;
	flex-flow: row;
	align-items: center;
	justify-content: center;
}

/* Screen width is too small to be usable */
@media (max-width: 300px) {

	header,
	main,
	#barba-wrapper {
		display: none !important;
	}

	.support {
		display: block;
	}
}

/* Screen height is too thin to be usable */
@media (min-aspect-ratio: 3/1) {

	header,
	main,
	#barba-wrapper {
		width: 100%;
		height: 100%;
		overflow: hidden;
		display: none !important;
	}

	body {
		width: 100%;
		height: 100%;
		overflow: hidden;
	}

	.support {
		display: flex;
	}

	.support h1 {
		margin: 0 var(--padl) 0 0;
		font-size: 20vh;
		line-height: 20vh;
	}

	.support p,
	.support h4 {
		padding: 0;
	}

	.support h4 {
		margin: 0;
		font-size: 8vh;
	}

	.support p {
		margin: 0 0 0 var(--padl);
		font-size: 5vh;
	}
}

@media (min-aspect-ratio: 5/1) {
	.support h1 {
		font-size: 30vh;
		line-height: 30vh;
	}

	.support h4 {
		font-size: 12vh;
	}

	.support p {
		font-size: 10vh;
	}
}


/* Global costum Style */

.red {
	color: var(--red);
}

#corner,
#back {
	position: fixed;
	display: none;
	z-index: 999999;
	pointer-events: none;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

.rounded body {
	background-color: #000;
}

#back {
	background-color: var(--white);
	z-index: 0;
}

.rounded #corner,
.rounded #back {
	display: block;
}

#corner div {
	position: fixed;
	top: 0;
	left: 0;
	width: var(--padm);
	height: var(--padm);
	background-color: #000;
	transform-origin: center;
	transform: translate3d(0, 0, 0);
	clip-path: polygon(100% 0, 75% 1%, 55% 4%, 33% 14%, 23% 23%, 14% 33%, 4% 55%, 1% 75%, 0 100%, 0 0);
}

#corner .tr {
	left: initial;
	right: 0;
	transform: rotate(90deg);
}

#corner .br {
	top: initial;
	bottom: 0;
	left: initial;
	right: 0;
	transform: rotate(180deg);
}

#corner .bl {
	top: initial;
	bottom: 0;
	transform: rotate(270deg);
}

/* Loading style */
#loader {
	position: fixed;
	z-index: 999999;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: transparent;
	color: var(--black);
}

#loader p {
	display: flex;
	flex-flow: row;
	align-items: center;
	justify-content: center;
	position: fixed;
	top: initial;
	left: var(--padx);
	right: var(--padx);
	bottom: var(--pady);
	font-size: .7rem;
	letter-spacing: .05rem;
	text-transform: uppercase;
	font-weight: 400;
	color: inherit;
}

#loader span:nth-child(2) {
	display: block;
	margin: 0 0 0 20px;
	padding: 1px;
	width: 200px;
	height: var(--pads);
	border: 1px solid var(--black);
	background-color: transparent;
	appearance: none;
	border-radius: calc(var(--pads) / 2);
}

#loader span i {
	display: block;
	height: 100%;
	background-color: var(--black);
	border-radius: calc((var(--pads) / 2) - 2px);
}

#loader .simple {
	position: fixed;
	top: initial;
	left: initial;
	bottom: var(--pady);
}

#loader .simple span {
	left: initial;
	height: calc(var(--pads) - 4px);
	border-radius: calc((var(--pads) - 4px) / 2);
	width: 100px;
	background-color: var(--gray-light);
}


/* Hidden and Fixed */
.hidden {
	display: none;
}

.fixed {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}


/* Arrow links */
.arrow-big,
.arrow {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	padding: 0;
	color: var(--black);
	white-space: nowrap;
}

.arrow:after {
	content: "";
	display: block;
	background: url(../img/arrow.svg) no-repeat 100% 50%;
}

.dark .arrow:after {
	background-image: url(../img/arrow-white.svg);
}

.arrow-big.center {
	right: 50%;
	transform: translate3d(-50%, 0, 0);
}

.arrow-big.center .arrow:after {
	width: 50px;
}

.arrow span {
	font-size: .9rem;
	line-height: 17px;
	letter-spacing: 0.05em;
	order: 0;
	white-space: nowrap;
	align-self: center;
	pointer-events: none;
}

.arrow-small .arrow:before,
.arrow-small .arrow:after {
	content: "";
	display: block;
	margin: 0;
	pointer-events: none;
}

.arrow-small .arrow:after {
	transform: none;
}

.arrow-small .arrow {
	flex-flow: row nowrap;
}


/* Year information */
.year {
	position: fixed;
	display: flex;
	margin: 0;
	padding: 0;
	align-items: flex-end;
	text-align: right;
	color: var(--black);
}


/* Header style */
header {
	opacity: 0;
	position: fixed;
	z-index: 3;
	height: 45px;
}

header .left,
header .right {
	mix-blend-mode: inherit;
}

header .left {
	display: flex;
	flex-direction: row;
	position: absolute;
	left: 0px;
}

header .right {
	display: flex;
	flex-direction: row;
	position: absolute;
	height: 44px;
	right: 0px;
}

/* Logo item */
.logo svg {
	width: 25.6px;
	height: 40px;
	pointer-events: none;
}

.logo svg g {
	fill: var(--black);
	transition: fill 1s ease;
}

.dark .logo.tas {
	filter: invert(100%);
}

/* Common styles between items */
.size,
.lamp,
.menu {
	flex: none;
	order: 0;
	align-self: center;
}

.no-js .size,
.no-js .lamp {
	display: none;
}

/* Text size item */
.size {
	margin-left: 0;
	color: var(--black);
}

/* Dark mode item */
.lamp {
	order: 1;
}

.lamp svg {
	width: 44px;
	height: 44px;
	mix-blend-mode: normal;
}

#sun,
#moon {
	display: block;
	opacity: 0;
	transition: opacity .5s ease;
}

#sun {
	stroke: var(--black);
	opacity: 1;
}

#sun circle {
	fill: var(--yellow);
}

.dark #sun {
	opacity: 0;
}

.dark #moon {
	opacity: 1;
}

/* Menu item and switch */
.menu {
	order: 2;
}

.menu .switch {
	position: relative;
	z-index: 2;
	display: flex;
	flex-direction: row;
}

.switch,
.switch svg,
.items .close,
.switch span {
	display: block;
	height: 44px;
	line-height: 44px;
	font-size: .9rem;
	letter-spacing: 0.05em;
	color: var(--black);
}

.switch svg,
.switch span {
	pointer-events: none;
}

.switch svg {
	stroke: var(--black);
	width: 32px;
	margin-right: var(--padm);
}

/* Slide right menu items */
.menu .overlay {
	position: fixed;
	z-index: 1000;
	display: none;
	align-items: flex-start;
	justify-content: flex-end;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0);
}

.menu .items {
	display: flex;
	height: 100%;
	width: calc(2 * var(--pic-w));
	max-width: 100%;
	padding: calc((2 * var(--pady)) + 44px) calc(2 * var(--padx)) calc(2 * var(--pady));
	background-color: var(--white);
	will-change: transform, opacity;
}

.dark .menu .items {
	background-color: var(--dark-white);
}

.items .close {
	position: absolute;
	top: var(--pady);
	left: initial;
	right: var(--padx);
	padding: 0 12px 0 48px;
	margin: 4px -12px;
	height: 36px;
	line-height: 36px;
	border-radius: 8px;
	transition: all .15s ease;
	will-change: background-color;
}

.dark .items .close:hover {
	color: var(--black);
}

.items .close span {
	display: inline-block;
	text-indent: -666rem;
}

.items .close span:after,
.items .close span:before {
	content: "";
	position: absolute;
	top: 50%;
	left: 24px;
	display: block;
	width: 20px;
	height: 2px;
	background-color: var(--black);
	border-radius: 2px;
	transform-origin: center;
	transform: translate3d(-50%, -50%, 0) rotate(45deg);
}

.items .close span:before {
	transform: translate3d(-50%, -50%, 0) rotate(-45deg);
}

.items .close:before,
.items .close:hover:before {
	background-color: var(--black-25);
}

.items ol {
	display: flex;
	flex-direction: column;
	width: 100%;
	margin: 0;
	padding: 0;
}

.items li {
	position: relative;
	display: block;
	flex-direction: row nowrap;
	margin: 0 0 var(--padl);
	padding: 0;
	list-style: none;
	font-size: 1.6rem;
	letter-spacing: 0.025em;
}

.items li.hr {
	display: block;
	width: 100%;
	height: 2px;
	background-color: var(--black-50);
	flex: none;
}

.items li:before,
.items li:after {
	content: "";
	display: block;
	position: absolute;
	z-index: 2;
	top: 50%;
	left: 0;
	pointer-events: none;
	transform: translate3d(0, -50%, 0);
}

.dark .items li:hover:before,
.dark .items li:hover:after {
	color: var(--white);
}

.items li:nth-child(1):before {
	content: "00."
}

.items li:nth-child(2):before {
	content: "01."
}

.items li:nth-child(4):before {
	content: "02."
}

.items li:last-child {
	margin: 0;
	padding: 0;
}

.items li:first-child,
.items li:last-child {
	margin-top: auto;
}

.items li:last-child {
	height: auto;
}

.items li:last-child p {
	font-size: .9rem;
}

.items li a {
	position: relative;
	z-index: 1;
	display: block;
	margin: calc(-1 * var(--pads)) calc(-2 * var(--pads));
	padding-left: calc(44px + (4 * var(--pads)));
	line-height: calc(var(--padl) + (2 * var(--pads)));
	height: calc(var(--padl) + (2 * var(--pads)));
	border-radius: 8px;
}


/* Common middle content */
.middle {
	position: relative;
	z-index: 2;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
}

.middle.clip {
	overflow: hidden;
}

.middle img.logo {
	max-width: 100%;
	height: 100%;
}

.pics {
	z-index: 2;
	pointer-events: none;
}

.middle .shade {
	position: absolute;
	background-color: var(--black);
	visibility: hidden;
	opacity: 0;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}


/* Split text style */
.splord,
.splext {
	padding: 0;
	margin: 0;
	display: inline-block;
}

/* Main text in Homepage and Me */
.main-text {
	display: flex;
	flex-flow: column;
}

.main-text h1 {
	font-size: 6rem;
	font-weight: 500;
	padding: 0 !important;
	line-height: 80%;
	color: var(--black);
	max-width: 700px;
}

.hi .main-text h1 {
	max-width: initial;
}

.main-text h1 a {
	display: inline-block;
	background: inherit;
}

@media (min-width: 1900px) {
	.main-text h1 {
		max-width: 800px;
	}

	.hi .main-text h1 {
		max-width: initial;
	}
}


/* Homepage footer */
.footer {
	position: fixed;
	z-index: 3;
	top: initial;
	bottom: 0;
	left: 0;
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
	align-items: flex-end;
	justify-content: space-between;
	pointer-events: none;
	color: var(--black);
}

.footer>* {
	display: flex;
	flex-direction: row;
	align-items: center;
