/* Background flare */
.flares {
	position: fixed;
	z-index: 0;
	overflow: hidden;
}

.flares img {
	position: absolute;
	transform: scale3d(1.25, 1.25, 1);
}

/* Purple */
.flare1 {
	top: 17.5%;
	left: -70%;
}

/* Red */
.flare2 {
	top: 30%;
	left: -25%;
}

/* Blue */
.flare3 {
	top: 17.5%;
	left: -10%;
}

/* Green */
.flare4 {
	top: 20%;
	left: 25%;
}

/* Yellow */
.flare5 {
	top: 10%;
	left: 40%;
}

/* Blue */
.flare6 {
	top: 15%;
	left: -80%;
}

/* Red */
.flare7 {
	top: 35%;
	left: -110%;
}


/* Detail page common style */
.detail picture {
	position: relative;
	display: block;
}

.dark .detail picture img {
	position: relative;
	z-index: 1;
}

.dark .detail picture .shadow {
	content: "";
	position: absolute;
	z-index: 0;
	display: block;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: .4;
	background-size: 100%;
	/*background-color: #333;*/
	transform: translate3d(-10px, 30px, 0);
	filter: blur(40px);
}

/* Style text on the left and image spread like fan */
.style-spread:after {
	z-index: 3;
	display: block;
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 85%;
	bottom: -1%;
	background: transparent;
	background: -moz-linear-gradient(0deg, rgba(255, 255, 255, 1) 10%, rgba(255, 255, 255, .75) 20%, rgba(255, 255, 255, 0) 100%);
	background: -webkit-linear-gradient(0deg, rgba(255, 255, 255, 1) 10%, rgba(255, 255, 255, .75) 20%, rgba(255, 255, 255, 0) 100%);
	background: linear-gradient(0deg, rgba(255, 255, 255, 1) 10%, rgba(255, 255, 255, .75) 20%, rgba(255, 255, 255, 0) 100%);
}

.dark .style-spread:after {
	background: transparent;
	background: -moz-linear-gradient(0deg, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, .75) 20%, rgba(0, 0, 0, 0) 100%);
	background: -webkit-linear-gradient(0deg, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, .75) 20%, rgba(0, 0, 0, 0) 100%);
	background: linear-gradient(0deg, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, .75) 20%, rgba(0, 0, 0, 0) 100%);
}

.style-spread,
.style-spread-left {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;
}

.style-spread .text {
	min-height: 50px;
}

.style-spread .thumbs {
	position: relative;
	flex: 1;
}

.style-spread picture {
	position: absolute;
	display: none;
}

.style-spread picture.pic-4 {
	z-index: 2;
	display: block;
}

.style-spread picture.pic-5 {
	z-index: 1;
	display: block;
}

.style-spread-left .text {
	order: 2;
}

.style-spread-left .thumb {
	order: 1;
}

/* Style text on top and Images in the bottom */
.style-top {
	height: auto;
}

.style-top,
.style-top-text {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: flex-start;
}

.style-top .text,
.style-top-text .text {
	display: flex;
	flex-flow: column nowrap;
}

.style-top .thumbs {
	position: relative;
	display: flex;
	flex-flow: row nowrap;
	margin: 0 -30px;
	flex: none;
}

.style-top picture {
	position: relative;
	align-self: flex-start;
}

/* Style text on top without image */
.style-top-text {
	justify-content: center;
}

/* Style with mansonry like layout with text on the right */
.style-masonry {
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.suzuki .style-masonry {
	height: auto;
}

.style-masonry .thumbs {
	display: flex;
	flex: none;
}

.style-masonry .text {
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-end;
}

/* Style that look like table */
.style-flex {
	display: flex;
	justify-content: center;
	align-items: center;
}

.style-row {
	display: flex;
	flex-direction: column;
	align-content: stretch;
}

.style-column {
	display: flex;
	flex-direction: row;
	align-items: flex-end;
}

.style-column .text small {
	line-height: 150%;
	letter-spacing: 0.05em;
}

/* Style with angled photo on the bottom */
.style-angled {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	overflow: hidden;
	justify-content: flex-start;
	background-color: var(--dark-white);
	transition: background-color .5s ease;
}

.style-angled .thumbs {
	display: flex;
	flex-flow: row nowrap;
}

.style-angled picture {
	flex: none;
}

.style-angled .pic-1,
.style-angled .pic-2,
.style-angled .pic-4 {
	order: 1
}

.style-angled .pic-3,
.style-angled .pic-8,
.style-angled .pic-9,
.style-angled .pic-10 {
	order: 2
}

.style-angled .pic-5,
.style-angled .pic-6,
.style-angled .pic-7 {
	order: 3
}

/* Style with logo on the bottom */
.style-bottom-logo .text,
.style-bottom-logo .thumbs {
	max-width: 964px;
}

.style-bottom-logo .text {
	margin-top: 110px;
	padding-right: 495px;
}

.style-bottom-logo .thumbs ul {
	pointer-events: none;
	margin: -300px 0 -85px;
	display: flex;
	align-items: flex-end;
}

.style-bottom-logo .thumbs li {
	flex: 1;
	padding-bottom: 160px;
}

.style-bottom-logo .thumbs li:last-child {
	padding-bottom: 0;
	margin-right: -40px;
}

.style-bottom-logo .thumbs li picture {
	box-shadow: none;
	padding-right: var(--padl);
}

.style-bottom-logo .thumbs li:last-child picture {
	width: 495px;
	margin: 0;
	padding-right: 0;
}

.dark .forcebright {
	background-color: #4f4f4f;
}

/* Style slideshow */
.style-slideshow {
	display: block;
	height: 100vh;
	padding: 0 !important;
	justify-content: flex-start;
	overflow: hidden;
}

.style-slideshow .before,
.style-slideshow .after {
	position: absolute;
	z-index: 2;
	top: 50vh;
	right: 50vw;
	content: "";
	display: block;
	width: 100px;
	height: 30px;
	margin-top: -15px;
	background: url(../img/arrow.svg) right center no-repeat;
	background-size: contain;
	cursor: pointer;
}

.dark .style-slideshow .before,
.dark .style-slideshow .after {
	background-image: url(../img/arrow-white.svg);
}

.style-slideshow .before {
	left: calc(50vw - 32.5vw - 100px);
	right: initial;
	transform: rotate(180deg);
}

.style-slideshow .after {
	left: initial;
	right: calc(50vw - 32.5vw - 100px);
}

.style-slideshow picture {
	width: 65vw;
}

.style-slideshow ul {
	white-space: nowrap;
	height: 100%;
}

.style-slideshow .thumbs,
.style-slideshow ul li {
	width: 100%;
	height: 100%;
}

.style-slideshow .thumbs {
	position: relative;
	z-index: 1;
	overflow-y: hidden;
	overflow-x: auto;
	height: 100%;
	margin-bottom: -50px;
	scroll-snap-type: x mandatory;
}

.style-slideshow ul li {
	display: flex;
	flex: none;
	float: left;
	align-items: center;
	justify-content: center;
	scroll-snap-align: start;
}

.style-slideshow-small .before {
	left: var(--padx);
	right: initial;
	transform: rotate(180deg);
}

.style-slideshow-small .after {
	left: initial;
	right: var(--padx);
	transform: none;
}

.style-slideshow-small picture {
	width: 60vw;
	transform: rotate(-3deg);
}

.style-slideshow-small ul li {
	width: auto;
	padding: 0 0 0 20vw;
}

.style-slideshow-small ul li:last-child {
	padding-right: 20vw;
}

@media screen and (min-width: 600px) {
	.style-slideshow-small picture {
		width: 27.5vw;
	}

	.style-slideshow-small ul li {
		padding-left: 15vw;
	}

	.style-slideshow-small ul li:last-child {
		padding-right: 15vw;
	}
}

@media screen and (min-width: 1000px) {
	.style-slideshow-small picture {
		width: 20vw;
	}

	.style-slideshow-small ul li {
		padding-left: 10vw;
	}

	.style-slideshow-small ul li:last-child {
		padding-right: 10vw;
	}
}

/* Style truncate */
.style-trunc {
	min-height: initial;
	height: auto;
	flex-direction: row;
	align-items: flex-end;
}

.style-trunc .wrap {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.style-trunc .text {
