	margin-right: var(--padxl);
}

/* Footer links in every detail page */
.links {
	position: relative;
	z-index: 1;
	display: flex;
	scroll-snap-align: end;
}

.links nav {
	display: flex;
	flex-flow: column wrap;
}

.links div {
	display: flex;
}

.links a {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.links a:hover {
	color: var(--red);
}

.links a:before,
.links a:after {
	content: "";
	display: inline-block;
}

.links a:before {
	border: 2px solid var(--black);
