}

/* Common ol/ul style with leading zero style */
.zero {
	display: flex;
	width: 100%;
	margin: 0;
	padding: 0;
}

.zero li {
	position: relative;
	display: flex;
	align-items: flex-start;
	flex: 1;
	padding: 0;
	line-height: 150%;
	letter-spacing: 0.02em;
	font-feature-settings: 'liga';
	color: var(--black-gray-light);
}

.zero li:before {
	content: "00";
	display: flex;
	flex: 0;
}

.zero li:nth-child(2):before {
	content: "01"
}

.zero li:nth-child(3):before {
	content: "02"
}

.zero li:nth-child(4):before {
	content: "03"
}

.zero li:nth-child(5):before {
	content: "04"
}

.zero li:nth-child(6):before {
	content: "05"
}

.zero li:nth-child(7):before {
	content: "06"
}

.zero li:nth-child(8):before {
	content: "07"
}

.zero li:nth-child(9):before {
	content: "08"
}

.zero li:nth-child(10):before {
	content: "09"
}

/* ol/ul with flat style */
.flat {
	display: flex;
	list-style: none;
	margin: 0;
	padding: 0;
	flex-flow: row nowrap;
}

/* ol/ul with without style */
.bland {
	display: flex;
	list-style: none;
	margin: 0;
	padding: 0;
	flex-flow: column nowrap;
}

/* ol/ul for branding color for Suzuki */
