	text-align: center;
	flex-flow: column;
	bottom: var(--pady);
	transform: translate(-50%, 0);
}

#post a {
	pointer-events: all;
}

#post svg {
	width: auto;
	height: 48px;
	fill: var(--black);
	margin-bottom: var(--pads);
	transition: .5 fill ease;
}

#post p {
	width: auto;
	text-align: center;
}

#post a,
#post svg {
	transition: color 1.28s ease-in, fill 1.28s ease-in;
}

#post:hover a,
#post:hover svg {
	color: var(--red);
	fill: var(--red);
	transition: color 0.16s ease-out, fill 0.16s ease-out;
}

.no-js #post {
	display: none;
}

.me .igstage {
	z-index: 3;
	will-change: transform;
	pointer-events: none;
}

.igstage {
	display: flex;
	align-items: center;
	justify-content: center;
}

.igstage .thumbs {
	display: flex;
	flex-flow: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	padding: 0;
	will-change: opacity, transform;
}

.igstage .thumbs a {
	position: relative;
	display: flex;
	padding: var(--padm);
	align-items: center;
	justify-content: center;
	z-index: 1;
	pointer-events: auto;
	will-change: opacity, transform;
}

.igstage .thumbs a img {
	pointer-events: none;
	transition: box-shadow .5 ease;
}

.igstage .thumbs a:after {
	content: "";
	z-index: 0;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	display: none;
	background: url(../img/drop-shadow.png) 0 0 no-repeat;
	background-size: 100% 100%;
	transform: scale3d(1.5, 1.5, 1);
	pointer-events: none;
}

.igstage .thumbs img {
	position: relative;
	z-index: 1;
	display: block;
	max-width: 100%;
	max-height: 100%;
	border-radius: var(--pads);
}

.igstage.scroll {
	min-height: auto;
	height: auto;
}

.igstage.scroll .thumbs {
	display: flex;
	flex-flow: row;
	flex-wrap: nowrap;
}

.igstage.scroll .thumbs a {
	width: 40vw;
	height: 40vw;
	margin: 0 var(--padm);
}

@media (min-width: 1200px) {
	.igstage.scroll .thumbs a {
		width: 30vw;
		height: 30vw;
	}
}

.cofound {
	z-index: 4;
}

.cofound .text {
	z-index: 2;
}

.cofound .thumbs {
	z-index: 1;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.dark .cofound .thumbs {
	background-color: var(--white);
}

.cofound picture {
	display: block;
	position: absolute;
	top: 0;
	left: 50%;
	opacity: .2;
	transform: translate3d(-50%, 0, 0);
	will-change: transform;
}

.cofound picture,
.cofound picture img {
	width: auto !important;
	height: auto !important;
	min-width: 100% !important;
	min-height: 100% !important;
}

.cofound a {
	background-color: var(--yellow);
	margin: var(--padm) 0 0 0;
	padding: var(--pads) var(--padm);
	border-radius: 8px;
	will-change: background-color;
}

.dark .cofound a {
	color: var(--white);
}

.cofound a:hover {
	background-color: transparent;
}

.dark .cofound a:hover {
	color: var(--black);
}

.cofound a:before,
.cofound a:hover:before {
	background-color: var(--white);
}

/* Say Hi page */
.hi h1,
.hi h1 a,
.hi h1 span,
.hi h1 small {
	display: flex;
	flex-direction: row nowrap;
}

.hi .main-text h1 {
	margin: 0;
	letter-spacing: -0.06em;
}

.hi h1 a {
	cursor: pointer;
}

.hi h1 span,
.hi h1 small {
	position: relative;
	pointer-events: none;
	will-change: opacity, color, transform;
}

.hi h1 small {
	position: absolute;
	align-items: flex-end;
	left: 0;
	top: 100%;
	font-size: .8rem;
	font-weight: 400;
	line-height: var(--padl);
	padding-left: calc((2 * var(--padm)) + var(--pads));
	height: var(--padxxl);
	opacity: 0;
	white-space: nowrap;
}

.hi h1 small:before,
.hi h1 small:after {
	position: absolute;
	left: var(--pads);
	top: 0;
	bottom: calc(var(--padl) / 2);
	content: "";
	display: block;
	border-left: 1px solid var(--black);
}

.hi h1 small:after {
	top: initial;
	padding-left: var(--padm);
	border-left: 0;
	border-top: 1px solid var(--black);
}

.hi .flares {
	display: flex;
	justify-content: center;
	align-items: center;
}

.hi .flares img {
	position: absolute;
	top: initial;
	left: initial;
	opacity: 0;
	width: auto;
	height: 75vh;
	transform: initial;
	will-change: transform, opacity;
}


/* Konami code */
#konamicode {
	position: fixed;
	z-index: 999;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, .85);
}

img.rounded {
	border-radius: var(--pads);
}


/* Plurk Statistics */

.plurk sub,
.plurk sup {
	font-size: inherit;
	top: initial;
	bottom: initial;
	line-height: 100%;
}

.plurk img {
	border-radius: 4px;
}

.plurk .debug {
	width: 75vw;
	overflow: auto;
	padding: 20px;
	background-color: #eee;
	margin: 0 auto;
}

.plurk .debug p,
.plurk .debug pre {
	font-size: 12px;
	line-height: 120%;
	font-family: monospace;
}

/*  Plurk - First section */
.plurk .first {
	text-align: center;
	background-color: var(--lime);
	transition: background-color .5s ease;
}

.plurk .first h1 {
	margin: var(--padl) 0 var(--padm) 0;
}

.plurk .bgtext {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 500;
	font-size: 70vw;
	letter-spacing: -10vw;
	color: var(--yellow);
	pointer-events: none;
	transform: translate(0, 0);
}

.plurk .bgtext sup,
.plurk .bgtext sub {
	transition: color .5s ease;
}

.plurk .bgtext sup {
	margin: -30vh 0 0 -10vw;
}

.plurk .bgtext sub {
	margin: 30vh 0 0 0;
}

.middle.grant {
	position: relative;
	z-index: 1;
}

/*  Plurk - Login */
#permission {
	z-index: 2;
}

#permission form {
	margin: 0;
	padding: var(--padl);
	max-width: 500px;
	background-color: var(--white);
	border-radius: var(--padm);
	overflow: hidden;
	box-shadow: -10px 30px 60px rgba(0, 0, 0, 0.25);
	transition: background-color .5s ease;
}

#permission h1 {
	color: var(--red);
	text-align: left;
	padding: 0 0 var(--padm);
	margin: 0;
}

#permission h1 sub,
#permission h1 sup {
	font-size: 75%
}

#permission h1 sub {
	top: 0rem;
}

#permission h1 sup {
	top: -1rem;
}

#permission ol {
	display: flex;
	flex-flow: column;
	z-index: -1;
	text-align: left;
	margin: 0;
	padding: 0;
}

#permission li {
	position: relative;
	margin: 0;
	padding: var(--padm) 0 var(--padm) var(--padl);
	list-style: none;
	border-top: 1px dashed var(--gray-light);
}

#permission li:before {
	position: absolute;
	left: 0;
}

#permission li:nth-child(1):before {
	content: "00";
}

#permission li:nth-child(2):before {
	content: "01";
}

#permission li:nth-child(3):before {
	content: "02";
}

#permission li:nth-child(4):before {
	content: "03";
}

#permission li.small * {
	font-size: .7rem;
}

#permission li.small {
	opacity: .8;
	padding-left: 0;
	padding-bottom: 0;
}

#permission li.small:before {
	content: ""
}

#permission p {
	margin: 0 0 8px;
}

#permission p:last-child {
	margin: 0;
}

#permission .cuddle {
	display: flex;
	flex-flow: row;
}

#permission input[type="text"] {
	max-width: 175px;
	flex: 1;
	padding: var(--pads) var(--padm);
	margin-right: var(--pads);
	border: 1px solid var(--gray-light);
	border-radius: 4px;
	text-align: center;
	transition: box-shadow .5s ease;
}

#permission input[type="text"]:focus {
	box-shadow: 0 0 40px rgba(0, 0, 0, .15);
}

#permission a,
#permission .submit {
	flex: 1;
	background-color: var(--red);
	color: var(--white);
	padding: var(--pads) var(--padl);
	margin-top: var(--pads);
	vertical-align: middle;
	text-align: center;
	text-decoration: none;
	border-radius: var(--pads);
}

#permission .submit {
	color: var(--black);
	background-color: var(--black-25);
	cursor: default;
	max-width: 150px;
	padding: var(--pads) 0;
	text-align: center;
}

#permission .submit.validated {
	background-color: var(--yellow);
	cursor: pointer;
}

#permission a:hover,
#permission .submit.validated:hover {
	color: var(--white);
	background-color: var(--black);
}

/* Plurk - Logout */
#logout {
	background-color: var(--yellow);
	color: var(--black);
	padding: var(--padm) var(--pads);
	border-radius: 8px;
	pointer-events: auto;
	cursor: pointer;
	transition: background .5s ease;
}

#logout:hover {
	background-color: var(--black);
	color: var(--white);
}

#logout svg {
	margin-right: var(--pads);
}

#logout svg rect {
	stroke: var(--black);
	transition: stroke .25s ease;
}

#logout svg path {
	fill: var(--black);
	transition: fill .25s ease;
}

#logout:hover svg rect {
	stroke: var(--white);
}

#logout:hover svg path {
	fill: var(--white);
}

/* Plurk - Statistics */
#statistics {
	position: relative;
	z-index: 2;
	height: initial;
	display: flex;
	flex-flow: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	background-color: var(--white);
	padding: var(--pady) calc((100vw - (var(--box) * var(--repeat) + var(--padl) * (var(--repeat)))) / 2) calc(var(--padxxl) + var(--pady));
	transition: background-color .5s ease;
	scroll-snap-align: end;
}

.statistics {
	display: flex;
	padding: calc(2.5 * var(--padl)) 0 calc(5 * var(--padl));
	margin: 0 auto;
	width: calc((var(--box) * var(--repeat)) + (var(--padl) * var(--repeat)));
	height: initial;
	min-height: initial;
	transition: background-color .5s ease;
}

.statistics.title,
.statistics.wrap {
	padding: 0;
	margin: calc(var(--padl) / 2);
}

.statistics.wrap {
	width: var(--box);
	height: calc(3/2 * var(--box));
	transition: background-color .5s ease;
	scroll-snap-align: center;
}

.statistics.title {
	position: relative;
	display: flex;
	height: auto;
	flex-flow: row;
	align-items: baseline;
	font-size: 2rem;
	color: var(--black);
	padding-top: var(--padxl);
	width: calc((var(--box) * var(--repeat)) + (var(--padl) * (var(--repeat) - 1)));
	scroll-snap-align: none;
}

.statistics.title h3 {
	display: flex;
	flex-flow: row;
	align-items: center;
}

.statistics.title span {
	display: flex;
	align-items: flex-start;
}

.statistics.title .line {
	flex: 1;
	height: 1px;
	margin-left: var(--pads);
	overflow: hidden;
	background-color: transparent;
	border-radius: 1px;
}

.statistics.title .line i {
	opacity: .5;
	display: flex;
	width: 100%;
	height: 100%;
	background-color: var(--black-25);
	border-radius: 1px;
	transition: background-color .5s ease;
}

.statistics.span2 {
	width: calc((var(--box) * 2) + (var(--padl) * 1));
}

.statistics.span3 {
	width: calc((var(--box) * 3) + (var(--padl) * 2));
}

.statistics .anim {
	z-index: 2;
	opacity: 0;
	width: 100%;
	height: 100%;
	border-radius: calc(2/3 * var(--padl));
	padding: var(--padl);
	background-color: var(--lime);
	transform-origin: center left;
	transition: box-shadow .24s ease-out, margin-top .24s ease-out;
}

.statistics p .text {
	line-height: 165%;
}

.statistics:hover .anim {
	position: relative;
	margin-top: calc(-2 * var(--padl));
	box-shadow: 0 var(--padm) var(--padxl) rgba(0, 0, 0, 0.5);
}

.statistics .capture {
	position: absolute;
	z-index: 1;
	width: 100%;
	height: 100%;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	color: var(--black);
	background-color: var(--yellow);
	border-radius: calc(2/3 * var(--padl));
	cursor: pointer;
}

.statistics .capture small {
	display: block;
	height: var(--padl);
	line-height: var(--padl);
	width: 100%;
	text-align: center;
	text-transform: uppercase;
	font-weight: 400;
	font-size: .6rem;
}

.statistics .anim p,
.statistics .anim a,
.statistics .anim span {
	color: var(--white);
	opacity: 1;
}

.statistics .anim>p,
.statistics .anim>a,
.statistics .anim>div {
	position: relative;
	display: flex;
	flex-flow: column;
	justify-content: flex-end;
	opacity: 1;
	height: 100%;
}

.statistics .anim span small {
	margin-top: var(--pads);
	letter-spacing: .05rem;
	line-height: 135%;
	opacity: .75;
}

.statistics .chart a,
.statistics .htmlcontent a,
.statistics .anim a.link {
	padding: 0 var(--pads);
	margin: 0 calc(-1 * var(--pads));
	border-radius: var(--pads);
	transition: background-color .12s ease-in-out;
}

.statistics .chart a,
.statistics .htmlcontent a {
	padding: var(--pads);
}

.statistics .chart a:hover,
.statistics .htmlcontent a:hover,
.statistics .anim a.link:hover {
	background-color: var(--black-25);
}

.statistics .anim .big {
	font-weight: 500;
	line-height: 100%;
	color: var(--white);
	display: block;
	margin-bottom: auto;
}

.statistics .post i {
	background-color: transparent;
	display: inline;
	font-style: italic;
	padding: 0;
	color: inherit;
}

.statistics img {
	display: inline-block;
	width: auto;
	height: auto;
}

.statistics .graph {
	flex: 1;
	display: flex;
	flex-flow: column;
	justify-content: flex-end;
	align-self: center;
	background-color: var(--white);
	margin-bottom: var(--padm);
	border-radius: 10000px;
	width: var(--padxxl);
	overflow: hidden;
	transition: background-color .5s ease;
}

.statistics .graph i {
	background-color: var(--yellow);
	border-radius: 0;
	height: 100%;
	transition: background-color .5s ease;
}

.statistics.avatar img {
	height: 64px;
	border-radius: 10000px;
}

.statistics.avatar .title {
	color: var(--white);
	display: inline;
	font-size: 1rem;
	text-transform: none;
	letter-spacing: .02em;
	text-align: left;
	margin-top: auto;
}

.statistics.avatar .htmlcontent {
	display: flex;
	flex-direction: column;
	flex: 1;
	justify-content: space-between;
	margin-bottom: var(--padm);
}

.statistics.drawuserlist .htmlcontent a {
	flex: 0;
}

.statistics .center {
	text-align: center;
}

.statistics .link {
	position: absolute;
	display: block;
	top: 0;
	right: 0;
	padding: 0;
	text-transform: uppercase;
}

.statistics .link svg {
	fill: var(--white);
	transition: fill .5s ease;
}

.grid .htmlcontent {
	display: flex;
	flex-flow: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	margin: auto calc(-1 * var(--padm));
}

.grid .htmlcontent div {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	margin: var(--padm);
}

.grid.emoticons .htmlcontent div:nth-child(10) {
	display: none;
}

.grid .htmlcontent img {
	flex: 0;
}

.grid .htmlcontent .count {
	position: absolute;
	top: 0;
	right: 0;
	transform: translate(75%, -75%);
	color: var(--black);
}

.grid .anim .title {
	text-transform: none;
}

.grid .anim .title i {
	text-transform: uppercase;
}

@media (min-width: 375px) {
	.grid .htmlcontent {
		margin: auto calc(-.75 * var(--padm));
	}
}

@media (min-width: 600px) {
	.grid.emoticons .htmlcontent div:nth-child(10) {
		display: flex;
	}
}

/* Plurk - Statistics - Loading */
.statistics.loading,
.statistics.loading .big {
	color: var(--white);
}

.statistics.loading .big {
	font-size: 4.5rem;
}

.statistics.loading i.month {
	display: table;
	margin-bottom: var(--pads);
}

.statistics.title,
.statistics.loading i {
	display: flex;
	line-height: 140%;
	font-size: .6rem;
	letter-spacing: .1rem;
}

.statistics.loading i {
	text-transform: uppercase;
}

.statistics.title {
	display: block;
	align-items: center;
	text-align: left;
	color: var(--white);
}

.statistics .chart {
	flex: 1;
	margin-bottom: var(--padl);
}

.statistics i,
.statistics.loading i {
	display: inline-block;
	padding: 4px 8px 2px;
	font-style: normal;
	line-height: 120%;
	color: var(--black);
	background-color: var(--white);
	border-radius: 4px;
	transition: background-color .5s ease;
}

.statistics .title {
	font-size: .8rem;
	line-height: 120%;
	color: var(--white);
}

.statistics .title i {
	display: inline-block;
}

.statistics .title strong {
	font-weight: normal;
	display: block;
	text-transform: initial;
	color: var(--white);
	letter-spacing: .05rem;
	opacity: .75;
	padding: 2px 0 0;
}

/* Plurk - Statistics - Spesific Style */
.postcontent img {
	width: auto;
	max-width: 64px;
	margin-right: 4px;
}

.postcontent .title {
	font-size: .6rem;
	display: flex;
	align-items: center;
	margin-top: var(--padm);
	line-height: initial;
}

.postcontent .title i {
	margin-right: auto;
	text-transform: uppercase;
}

.postcontent .emoticon_my {
	max-width: initial;
}

.postcontent .post {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	overflow: hidden;
}

.chart {
	position: relative;
}

.chart a {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	margin: auto 0;
	white-space: nowrap;
	display: flex;
	flex-flow: row;
	transform: translate(0, -50%);
}

.chart .plurkers,
.statistics .plurkers {
	display: flex;
	justify-content: space-around;
	flex: 1;
}

.chart .plurkers .name,
.statistics .plurkers .name {
	margin-right: auto;
	line-height: 24px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.grid .count,
.chart .plurkers .count,
.statistics .plurkers .count {
	color: var(--black);
	background-color: var(--yellow);
	line-height: 24px;
	height: 24px;
	min-width: 24px;
	text-align: center;
	border-radius: 24px;
	font-size: .7rem;
	font-weight: 500;
	padding: 0 var(--pads);
	transition: background-color .5s ease;
}

.chart .plurkers .avatar img,
.statistics .plurkers .avatar img {
	height: 24px;
	margin-right: var(--pads);
}

/* Center style */
.statistics.center .big {
	display: block;
	margin: var(--padl) auto auto;
	text-align: center;
	line-height: 100%;
}

.statistics.center .text {
	text-align: center;
}

/* Hashtag style */
.hashtags .htmlcontent {
	display: flex;
	flex-direction: column;
	flex: 1;
	justify-content: space-between;
	padding-bottom: var(--padm);
}

.hashtags .htmlcontent>div {
	display: flex;
	flex-direction: row;
	margin: 0;
	border-bottom: 1px dashed var(--white);
}

.hashtags .htmlcontent>div:last-child {
	margin-bottom: auto;
	border-bottom: 0;
}

.hashtags .htmlcontent strong {
	display: block;
	text-align: center;
	margin: 0 auto auto;
	font-size: var(--padl);
	width: var(--padxl);
	height: var(--padxl);
	line-height: var(--padxl);
	color: transparent;
	font-weight: 200;
	border-radius: 1000px;
	background-color: var(--white);
	transition: box-shadow .24s ease-out;
}

.hashtags:hover strong {
	box-shadow: inset 0 var(--padm) var(--padxl) rgba(0, 0, 0, 0.5);
}

.hashtags .htmlcontent a {
	flex: 1;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	transition: background-color .24s ease-in-out, color .24s ease-in-out;
}

.hashtags .htmlcontent .count {
	display: inline-block;
	background-color: var(--yellow);
	color: var(--black);
	padding: 0 var(--pads);
	border-radius: 10000px;
	font-size: .7rem;
	transition: color .2s ease-in-out, background-color .2s ease-in-out;
}

.hashtags .htmlcontent a:hover+.count {
	background-color: var(--lime);
}

.hashtags .title i {
	text-transform: uppercase;
}

/* Bubble style */
.bubble .count {
	display: none;
}

.bubble .htmlcontent {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-evenly;
	align-items: center;
	margin: auto 0;
}

.bubble .htmlcontent .plurkers {
	display: flex;
	flex-direction: column;
	margin: auto 0 var(--pads);
}

.bubble .plurkers:nth-child(1) {
	order: 3
}

.bubble .plurkers:nth-child(2) {
	order: 2
}

.bubble .plurkers:nth-child(3) {
	order: 3
}

.bubble .plurkers:nth-child(4) {
	order: 4
}

.bubble .plurkers:nth-child(5) {
	order: 1
}

.bubble .plurkers span {
	width: 100%;
	text-align: center;
}

.bubble .plurkers .avatar {
	margin-bottom: var(--pads);
}

.bubble .plurkers .avatar img {
	width: var(--padl);
	height: auto;
	border-radius: 100%;
}

.bubble .plurkers:nth-child(1) .avatar img {
	width: calc(3 * var(--padl))
}

.bubble .plurkers:nth-child(2) .avatar img {
	width: calc(2.5 * var(--padl))
}

.bubble .plurkers:nth-child(3) .avatar img {
	width: calc(2 * var(--padl))
}

.bubble .plurkers:nth-child(4) .avatar img {
	width: calc(1.5 * var(--padl))
}

.bubble .title {
	color: var(--white);
	text-transform: none;
	font-size: 1rem;
}

/* Listing attach style */
.attach .title i {
	text-transform: uppercase;
}

/* Responsed style */
.statistics.responded .big {
	display: none;
}

.statistics.responded .text {
	margin: auto var(--padxl);
	text-align: center;
	font-size: 1.5rem;
	line-height: 150%;
	letter-spacing: 0;
}

/* Posted style */
.statistics.posted .text {
	text-align: center;
}

.statistics.posted .big {
	position: relative;
	background-color: var(--yellow);
	color: var(--black);
	font-size: 5rem;
	letter-spacing: -.25rem;
	padding: 0 calc(3/4 * var(--padxl));
	min-width: calc(var(--box) * .5);
	height: calc(var(--box) * .4);
	line-height: calc(var(--box) * .4);
	border-radius: 10000px;
}

.statistics.posted .big:after {
	position: absolute;
	content: "";
	bottom: 0;
	right: 0;
	border-left: var(--padm) solid transparent;
	border-right: var(--padm) solid transparent;
	border-top: var(--padl) solid var(--yellow);
	border-bottom: var(--padl) solid transparent;
	transform: translate(15%, 50%) rotate(-40deg);
}

/* Coins */
.statistics.coins .big {
	background-color: var(--yellow);
	color: var(--black);
	width: calc(var(--box) - (2 * var(--padl)));
	height: calc(var(--box) - (2 * var(--padl)));
	line-height: calc(var(--box) - (2 * var(--padl)));
	border-radius: 100%;
}

/* Badges */
.statistics.badges .big {
	font-size: 5rem;
	letter-spacing: -.25rem;
	background-color: var(--yellow);
	color: var(--black);
	width: calc(1/2 * var(--box));
	height: calc(4/7 * var(--box));
	line-height: calc(4/7 * var(--box));
	padding: 0 var(--padm) calc(3/4 * var(--padxl));
	border-radius: var(--padl) var(--padl) 100% 100%;
}

/* MVP */
.mvp .htmlcontent .avatar {
	z-index: 1;
	position: relative;
	width: auto;
	margin: 0 auto var(--pads);
}

.mvp .htmlcontent .avatar img {
	margin-right: 0;
}

.mvp .htmlcontent .avatar:after {
	content: "";
	display: block;
	position: absolute;
	bottom: calc(-1 * var(--pads));
	right: calc(-1 * var(--pads));
	line-height: 100%;
	font-size: 1.5rem;
	padding: 0;
	border-radius: 100%;
}

.mvp .htmlcontent a:nth-child(1) .avatar:after {
	content: "";
	font-size: 3rem;
}

.mvp .htmlcontent a:nth-child(2) .avatar:after {
	content: "";
	font-size: 2.5rem;
}

.mvp .htmlcontent a:nth-child(3) .avatar:after {
	content: "";
	font-size: 2rem;
}

/* Anniversary */
.statistics.anniversary .big {
	display: flex;
	flex-direction: column;
	width: calc(1/2 * var(--box));
	height: calc(4/7 * var(--box));
	background-color: var(--yellow);
	overflow: hidden;
	border-radius: var(--padm);
}

.statistics.anniversary .big strong {
	display: flex;
	flex-direction: row;
	padding: var(--pads) var(--padm);
	font-size: 1rem;
	letter-spacing: .015rem;
	line-height: 100%;
	text-transform: uppercase;
	border-bottom: 1px solid var(--black-25);
}

.statistics.anniversary .big strong i {
	display: flex;
	padding: 0;
	font-weight: 400;
	background-color: transparent;
	margin: 0 0 0 auto;
	color: var(--black);
}

.statistics.anniversary .big strong i:first-child {
	margin-left: 0;
}

.statistics.anniversary .big em {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	color: var(--black);
	background-color: var(--white);
	font-style: normal;
}

/* Shared links */
.statistics.previewlinks a {
	display: block;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

/* Shared pics */
.statistics.previewpics .anim {
	overflow: hidden;
}

.statistics.previewpics .htmlcontent {
	position: absolute;
	z-index: 1;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: calc(-1 * var(--padl));
}

.statistics.previewpics .title {
	position: relative;
	z-index: 2;
	text-transform: uppercase;
	color: var(--white);
	margin: 0 0 auto;
}

.statistics.previewpics .box {
	position: relative;
	display: flex;
	flex-flow: column;
	width: 100%;
	height: 100%;
}

.statistics.previewpics .box .image {
	position: absolute;
	z-index: 1;
	width: 100%;
	height: 100%;
	opacity: .5;
	background-size: contain;
}

.statistics.previewpics .box .post {
	position: relative;
	z-index: 2;
	padding: var(--padxl) var(--padl) var(--padl);
	margin-top: auto;
	color: var(--white);
	line-height: 120%;
	background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .5) 75%);
}

.dark .statistics.previewpics .box .post {
	background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, .5) 75%);
}

.statistics.previewpics .post a {
	color: inherit;
}

.statistics.previewpics .post .ex_link.meta {
	display: inline-block;
	text-overflow: ellipsis;
	white-space: nowrap;
	max-width: 100%;
	overflow: hidden;
}

.statistics.previewpics .post .ex_link.meta img {
	margin-right: 6px;
}

.statistics .box .post br,
.statistics .box .post img,
.statistics .box .post .pictureservices {
	display: none;
}

.statistics .post div.meta {
	border-top: 1px dashed var(--white);
	display: flex;
	align-items: center;
	margin-top: var(--pads);
	padding-top: var(--padm);
}

.statistics div.meta span {
	display: block;
	margin-right: var(--padl);
}

.statistics div.meta .link {
	position: relative;
	margin-left: auto;
}

/* Shared links */
.statistics.sharedlinks .text {
	height: 100%;
}

.statistics.sharedlinks .title {
	font-size: .9rem;
}

.statistics.sharedlinks .post {
	margin-top: auto;
	line-height: 150%;
	color: var(--white);
	font-size: .75rem;
}

.statistics.sharedlinks .post br {
	display: inline-block;
}

.statistics.sharedlinks .info a {
	display: inline-block;
	color: inherit;
	padding: 2px 6px;
	background-color: var(--white);
	color: var(--black);
	border-radius: 4px;
	text-overflow: ellipsis;
	white-space: nowrap;
	max-width: 100%;
	overflow: hidden;
}

.statistics.sharedlinks .info a:hover {
	background-color: var(--yellow);
}

.statistics.sharedlinks div.meta {
	padding-top: var(--pads);
	justify-content: space-between;
}

.statistics.sharedlinks .meta .link,
.statistics.sharedlinks .meta span {
	font-size: .65rem;
	margin: 0;
}


/* Plurk - Statistics - Costum Page Style */
#hello {
	padding: 0;
	pointer-events: none;
	background-color: transparent;
}

#hello .animate {
	pointer-events: auto;
	background-color: var(--lime);
	transition: background .5s ease;
}

/* Credits style */
#credits {
	background-color: var(--yellow);
	opacity: 0;
	position: fixed;
	z-index: 0;
	left: 0;
	top: initial;
	bottom: 0;
	justify-content: flex-end;
}

#credits .text {
	max-width: 600px;
}

#credits p {
	margin: var(--pads) 0;
	line-height: 150%;
}

#credits .like,
#credits .noaffiliation {
	color: var(--black-75);
}

#credits .noaffiliation {
	font-size: .8rem;
	letter-spacing: .025rem;
	line-height: 1.25rem;
}

#credits a {
	text-decoration: none;
	transition: color .125s ease-in-out, background-color .125s ease-in-out;
	color: inherit;
	border-bottom: 1px dashed var(--black-75);
	columns: inherit;
	color: var(--black);
}

#credits a:hover {
	color: var(--red);
}

#credits .made {
	font-weight: 500;
	opacity: 1;
}

#credits .likeandsupport {
	display: flex;
	justify-content: space-between;
	padding-bottom: var(--padm);
	margin: var(--padm) 0 var(--padm);
	border-bottom: 1px dotted var(--black-50);
}

#credits .likeandsupport a,
#credits .likeandsupport i {
	display: block;
	font-style: initial;
	border: none;
	padding: 0;
}

#credits .likeandsupport a {
	display: flex;
	width: 30%;
	background-color: var(--black-15);
	padding: var(--padm);
	border-radius: var(--padm);
	flex-direction: column;
	align-items: center;
	font-size: 4rem;
	line-height: 100%;
	transition: background-color .125s ease-in-out;
}

#credits .likeandsupport a:hover {
	background-color: var(--white);
}

#credits .likeandsupport a i {
	margin-top: var(--pads);
	text-align: center;
	font-size: .75rem;
	line-height: 130%;
}

/* Back to top */
#backtotop {
	position: relative;
	color: var(--black);
	font-size: 2rem;
	text-align: center;
	width: 100%;
	height: calc(1/3 * var(--box));
	line-height: calc(1/3 * var(--box));
	margin: var(--padxxl) auto 0;
	cursor: pointer;
	transition: color .125s ease-in-out;
}

#backtotop:before {
	position: absolute;
	content: "";
	display: block;
	z-index: -1;
	width: calc(1/3 * var(--box));
	height: calc(1/3 * var(--box));
	border: 1px dashed var(--black-75);
	border-radius: 100%;
	transition: background-color .125s ease-in-out;
}

#backtotop:hover {
	color: var(--white);
}

#backtotop:hover:before {
	background-color: var(--black);
}

